import React, {Component} from 'react'

export default class Comment extends Component {
	constructor() {
		super()
		this.state = {
			existTimeStr: ''
		}
	}

	componentWillMount() {
		this.renderTime()
		this.timer = setInterval(() => {
			this.renderTime()
		}, 5000)
	}

	componentWillUnmount() {
		clearInterval(this.timer)
	}

	deleteComment() {
		this.props.onDeleteItem(this.props.idx)
	}

	renderTime() {
		const {itemInfo} = this.props
		const existTime = (Date.now() - itemInfo.createTime) / 1000
		const existTimeStr = existTime < 60
								? parseInt(Math.max(existTime, 1)) + 'ÁßíÂâç'
								: existTime < 3600
									? (parseInt(existTime / 60) + 'ÂàÜÈíüÂâç')
									: (parseInt(existTime / 3600) + 'Â∞èÊó∂Ââç')
		this.setState({
			existTimeStr: existTimeStr
		})
	}

	render() {
		const {itemInfo} = this.props
		return (
			<div>{itemInfo.userName}: {itemInfo.userContent} ‚Äî‚Äî ÂèëÂ∏É‰∫é {this.state.existTimeStr} <button onClick={this.deleteComment.bind(this)}>Âà†Èô§</button></div>
		)
	}
}