import React, {Component} from 'react'

export default class CommentInput extends Component {
	constructor() {
		super()
		this.state = {
			userName: '',
			userContent: ''
		}
	}

	componentWillMount() {
		this.loadUserName()
	}

	componentDidMount() {
		this.commentInput.focus()
	}

	loadUserName() {
		const userName = localStorage.getItem('userName')
		userName && this.setState({
			userName
		})
	}

	handleUserName(event) {
		const userName = event.target.value
		this.setState({
			userName: userName
		})
	}

	handleBlur() {
		localStorage.setItem("userName", this.state.userName)
	}

	handleUserContent(event) {
		const userContent = event.target.value
		this.setState({
			userContent: userContent
		})
	}

	handleSubmit() {
		const {userName, userContent} = this.state
		const createTime = Date.now()
		this.props.inputCallback(userName, userContent, createTime)
		this.setState({
			userName: userName,
			userContent: ''
		})
	}

	render() {
		return (
			<div>
				<span>Áî®Êà∑ÂêçÔºö</span>
				<input value={this.state.userName} onChange={this.handleUserName.bind(this)} onBlur={this.handleBlur.bind(this)} />
				<hr/>
				<span style={{color: 'red'}}>ËØÑËÆ∫ÂÜÖÂÆπÔºö</span>
				<input ref={(input) => this.commentInput = input} value={this.state.userContent} onChange={this.handleUserContent.bind(this)} />
				<hr />
				<button style={{backgroundColor: '#39a2d2', color: 'white'}} onClick={this.handleSubmit.bind(this)}>ÂèëÂ∏É</button>
			</div>
		)
	}
}