import React, {Component} from 'react'
import CommentInput from './CommentInput'
import CommentList from './CommentList'

export default class CommentApp extends Component {
	constructor() {
		super()
		this.state = {
			commentList: []
		}
	}

	componentWillMount() {
		this.loadComments()
	}

	loadComments() {
		const comments = localStorage.getItem('comment')
		comments && this.setState({
			commentList: JSON.parse(comments)
		})
	}

	saveComments() {
		localStorage.setItem('comment', JSON.stringify(this.state.commentList))
	}

	handleDeleteComment(idx) {
		const {commentList} = this.state
		commentList.splice(idx, 1)

		this.setState({
			commentList
		})
		this.saveComments()
	}

	handleInputCallback(userName, userContent, createTime) {
		if(!userName) {
			alert('enter userName')
		}

		if(!userContent) {
			alert('enter userContent')
		}

		this.state.commentList.push({userName, userContent, createTime})
		this.setState({
			commentList: this.state.commentList
		})
		this.saveComments()
		console.log(userName, ' + ', userContent)
	}

	render() {
		const {commentList} = this.state
		return (
			<div>
				<CommentInput inputCallback={this.handleInputCallback.bind(this)}/>
				<CommentList onDeleteComment={this.handleDeleteComment.bind(this)} commentList={commentList}/>
			</div>
		)
	}
}