import React, {Component} from 'react'
import Comment from './Comment'

export default class CommentList extends Component {
	handleDeleteItem(idx) {
		this.props.onDeleteComment(idx)
	}

	render() {
		const {commentList} = this.props
		console.log('commentList.js =„ÄãcommentList', commentList)
		return (
			<div>
				{
					!!commentList.length && commentList.map((item, index) => {
						return <Comment key={index} idx={index} itemInfo={item} onDeleteItem={this.handleDeleteItem.bind(this)} />
						// return <div key={idx}>{item.userName}: {item.userContent}</div>
					})
				}
			</div>
		)
	}
}